import { Injectable } from '@angular/core';
import { ConstantsService } from 'src/app/services/constants.service';
import { UtilsService } from 'src/app/services/utils.service';
import { Observable, throwError } from 'rxjs';
import { AngularFireDatabase } from '@angular/fire/database';

@Injectable({
  providedIn: 'root'
})
export class KhoManagementService {
  heightTable = "775";
  columnsTableProduct = [
    { title: "id", field: "id", align: "left", width: 100 },
    { title: "Tên sản phẩm", field: "Tên sản phẩm", align: "left", width: 300 },
    { title: "Quy cách", field: "Quy cách", align: "left", width: 100 },
    { title: "Phân loại", field: "Phân loại", align: "left", width: 300 },
    { title: "Số lượng", field: "Số lượng", align: "left", width: 100 },
    { title: "Giá nhập", field: "Giá nhập", align: "left", width: 100 },
    {
      title: "Ngày sản xuất", field: "NSX", align: "left", width: 130, formatter: (cell) => {
        return this.utilsService.getDateFromStringDD(cell.getValue(), "ddd MMM D YYYY HH:mm:ss Z");
      }
    },
    {
      title: "Hạn sử dụng", field: "HSD", align: "left", width: 130, formatter: (cell) => {
        return this.utilsService.getDateFromStringDD(cell.getValue(), "ddd MMM D YYYY HH:mm:ss Z");
      }
    },
    {
      title: "Ngày hết hạn", field: "Ngày hết hạn", align: "left", width: 130, formatter: (cell) => {
        return this.utilsService.getDateFromStringDD(cell.getValue(), "ddd MMM D YYYY HH:mm:ss Z");
      }
    },
    { title: "Ngày nhập kho", field: "Ngày nhập kho", align: "left", width: 136 },
    {
      width: 100, formatter: "buttonCross", align: "center", title: "Xóa sản phẩm", headerSort: false, cellClick: (e, cell) => {
        this.utilsService.presentAlertConfirmDelete(cell.getRow()._row.data.id, "Thông báo xóa sản phẩm", ' <strong>Sản phẩm sẽ bị xóa</strong>', "ProductDetails");
        event.stopPropagation();
      }
    }
  ];
  listProductDetails: any[] = [];
  constructor(private constantsService: ConstantsService,
    private utilsService: UtilsService,
    public db: AngularFireDatabase) {
    this.getListProduct();
  }
  getListProduct() {
    let listProDetails = this.db.list("Kho/");
    listProDetails.valueChanges().subscribe(product => {
      this.listProductDetails = [];
      if (product) {
        for (let index = 0; index < product.length; index++) {
          const element: any = product[index];
          this.listProductDetails.push(element);
        }
      }
    });
  }

  importToDB(dataInsert: any) {
    return Observable.create(observer => {
      let addNewData = this.db.object("/");
      let dataDelete = {};
      dataDelete[`NewProduct/`] = null;
      addNewData.update(dataDelete).then(result => {
        addNewData.update(dataInsert);
        observer.next(true);
        observer.complete();
      },
        error => {
          throwError(error);
        });
    },
      error => {
        throwError(error);
      });
  }
}
