import { Component, OnInit, ViewChild } from '@angular/core';
import { PopoverController } from '@ionic/angular';
import { UtilsService } from 'src/app/services/utils.service';
import { ModalImportCsvComponent } from 'src/app/components/modal-import-csv/modal-import-csv.component';
import { AppValueService } from 'src/app/services/app-value.service';
import { TranslateService } from '@ngx-translate/core';
import { KhoManagementService } from './kho-management.service';
import { ModalEditDetailProductComponent } from 'src/app/components/modal-edit-detail-product/modal-edit-detail-product.component';
@Component({
  selector: 'app-kho-management',
  templateUrl: './kho-management.page.html',
  styleUrls: ['./kho-management.page.scss'],
})
export class KhoManagementPage implements OnInit {
  @ViewChild('contentSearch') contentSearch: any;  
  constructor(private popoverController: PopoverController,
    public khoService: KhoManagementService,
    public utilsService: UtilsService,
    public appValueService: AppValueService,
    private translate: TranslateService) {
  }
  ngOnInit() {
  }
  async showModalImport(ev: any) {
    const popover = await this.popoverController.create({
      component: ModalImportCsvComponent,
      event: ev,
      translucent: true,
      componentProps: { mode: "productDetails", sheetName: "" },
      cssClass: "import-csv-popover"
    });
    popover.onDidDismiss()
      .then((result) => {
        if (result.data && result.data != "close") {
          this.utilsService.loadingStart();
          this.khoService.importToDB(result.data).subscribe((result) => {
            this.utilsService.loadingStop();
            if (result == true) {
              this.utilsService.showInfoSystem("Import thành công!");
            }
            else {
              this.utilsService.showError("Import thất bại!");
            }
          },
            error => {
              this.utilsService.showError(error);
            });
        }
        else {
          return true;
        }
      },
        error => {
          this.utilsService.showError(error);
        }
      );
    return await popover.present();
  }

  showDetail(data: any) {
    this.showModalEditProduct(data.ev, data.row._row.data);
  }
  showAdd(ev) {
    this.showModalEditProduct(ev, {});
  }
  async showModalEditProduct(ev: any, row: any) {
    const popover = await this.popoverController.create({
      component: ModalEditDetailProductComponent,
      id: "editDetailsProductModal",
      event: ev,
      translucent: true,
      componentProps: { rowProduct: row },
      cssClass: "edit-detail-product-popover"
    });
    return await popover.present();
  }

  ionViewDidEnter() {
    let removeHeight = [];
    this.appValueService.APP_TABULATOR_HEIGHT = [this.contentSearch, removeHeight];
    this.khoService.heightTable = this.utilsService.settingTableHeight(this.appValueService.APP_TABULATOR_HEIGHT);
  }
}
